import {
  ColoredValue,
  numberParse,
  ProgressBar,
  QuestionMarkTooltip,
  Tag,
} from '@aperture/uikit';
import { Fragment, useCallback, useMemo } from 'react';
import { StrategyType } from '../../../../../../constants/crosschain';
import { ProtocolInfo } from '../../../../../../constants/protocolConfig';
import {
  Strategy,
  StrategyInfo,
} from '../../../../../../constants/strategy/index';
import { TokenInfo } from '../../../../../../constants/tokenConfig';
import {
  calculateThreshold,
  getVaultName,
  numberParseLarge,
  numberToPercentage,
} from '../../../../../../helpers/utilities';
import { NumberCounter } from '../../../NumberCounter';
import './info.scss';

interface ICardProps {
  walletConnected: boolean;
  cardInfo: StrategyInfo | null;
  holdings: number;
  profits: string[];
  pageType: 'Portfolio' | 'Details' | 'DeltaNeutral';
  tvlGteCapacity: boolean;
  apy_7days: number | null;
  apy_season: number | null;
  inceptionDate: string;
  strategy: Strategy;
}

export const CardInfo = (props: ICardProps) => {
  const {
    cardInfo: data,
    walletConnected: connected,
    pageType: page,
    holdings,
    profits = ['', '--'],
    apy_7days,
    apy_season,
    inceptionDate,
    strategy,
    tvlGteCapacity,
  } = props;

  const getFarmingPoolInfo = (
    tokens: TokenInfo[],
    protocols: ProtocolInfo[]
  ) => {
    const tokenString = getVaultName(tokens);
    if (protocols.length === 2)
      return (
        <>
          {`${protocols[0].name} ${tokenString}`}
          <br />
          {`${protocols[1].name} ${tokenString}`}
        </>
      );
    else return <>{`${protocols[0].name} ${tokenString}`}</>;
  };

  const getColoredValue = (
    name: string,
    values: ICardProps,
    text: string | JSX.Element | undefined
  ) => {
    switch (name) {
      case 'APY':
        return (
          <ColoredValue value={Number(values.cardInfo?.apy)}>
            {text}
          </ColoredValue>
        );
      case 'APY_W':
        return (
          <ColoredValue value={Number(values.apy_7days)}>{text}</ColoredValue>
        );
      case 'APY_S':
        return (
          <ColoredValue value={Number(values.apy_season)}>{text}</ColoredValue>
        );
      case 'PROFITS':
        return <ColoredValue value={Number(profits[0])}>{text}</ColoredValue>;
      default:
        return <>{text}</>;
    }
  };

  const FormatTVL = useCallback(
    () => (
      <>
        <div style={{ display: 'flex' }}>
          <div style={{ paddingTop: '4px', marginRight: '8px' }}>
            {data?.TVL !== null && data?.TVL !== undefined ? (
              <NumberCounter
                value={data.TVL}
                formatNumberFunction={(value) =>
                  `$${numberParseLarge(value, 2)}`
                }
              />
            ) : (
              '-'
            )}
            {' / '}
            {data?.capacity !== null && data?.capacity !== undefined ? (
              <NumberCounter
                value={Number(data?.capacity)}
                formatNumberFunction={(value) => numberParseLarge(value, 2)}
              />
            ) : (
              '-'
            )}
          </div>
          {data?.TVL !== null &&
            data?.TVL !== undefined &&
            data?.capacity !== null &&
            data?.capacity !== undefined && (
              <ProgressBar
                percentage={Number(data?.TVL) / Number(data?.capacity)}
              />
            )}
        </div>
      </>
    ),
    [data]
  );

  const FormatAPY = (apy_value: number | null | undefined) => {
    return (
      <>
        {apy_value !== null && apy_value !== undefined ? (
          <NumberCounter
            value={apy_value}
            formatNumberFunction={(value) =>
              `${numberParse(value, 'percentage')}`
            }
          />
        ) : (
          '--%'
        )}
      </>
    );
  };

  const FormatHoldings = () => {
    return (
      <>
        {connected && !Number.isNaN(holdings) ? (
          <NumberCounter
            value={holdings}
            formatNumberFunction={(value) =>
              `${numberParseLarge(value, value > 0.1 ? 2 : 4)} ${
                strategy.tokens[0].ticker
              }`
            }
          />
        ) : (
          '-'
        )}
      </>
    );
  };

  const FormatCurrentValue = () => {
    return (
      <>
        {!Number.isNaN(holdings) ? (
          <NumberCounter
            value={holdings}
            formatNumberFunction={(value) => `$${numberParseLarge(value, 2)}`}
          />
        ) : (
          '-'
        )}
      </>
    );
  };

  const renderStrategy = (strategyType: StrategyType) => {
    switch (strategyType) {
      case StrategyType.PSEUDO_DELTA_NEUTRAL:
        return <Tag variant={'crab'} />;
      case StrategyType.PSEUDO_1X_LONG:
        return <Tag variant={'bull'} />;
    }
  };

  const FormatStrategy = () => {
    return (
      <>{strategy.strategyType ? renderStrategy(strategy.strategyType) : '-'}</>
    );
  };

  const FormatLeverage = useCallback(
    () => (
      <>
        {data?.leverage ? (
          <NumberCounter
            value={data.leverage}
            formatNumberFunction={(value) => `${value}X`}
          />
        ) : (
          '-'
        )}
      </>
    ),
    [data]
  );

  const FormatThreshold = useCallback(() => {
    const threshold = data
      ? calculateThreshold(data.priceMovementThreshold, data.leverage)
      : `-`;

    return (
      <NumberCounter
        value={threshold}
        formatNumberFunction={(value) =>
          typeof value === 'number'
            ? `${numberToPercentage(Number(value))}`
            : value
        }
      />
    );
  }, [data]);

  const infoProperty = {
    TVL: FormatTVL(),
    APY_TITLE: undefined,
    APY: FormatAPY(Number(data?.apy)),
    APY_W: FormatAPY(apy_7days),
    APY_S: FormatAPY(apy_season),
    HOLDINGS: FormatHoldings(),
    PROFITS: profits[1],
    CURRENT_VALUE: FormatCurrentValue(),
    STRATEGY: FormatStrategy(),
    LEVERAGE: FormatLeverage(),
    INCEPTION_DATE: inceptionDate === '' ? '--' : inceptionDate,
    THRESHOLD: FormatThreshold(),
    FARMING_POOL: getFarmingPoolInfo(strategy.tokens, strategy.protocols),
  };

  interface ICardInfo {
    name: keyof typeof infoProperty;
    title: string;
    titleClass?: string;
  }

  const APYClassName = useMemo(() => 'overallInfo-title indent', []);

  const dnProperty: ICardInfo[] = [
    { name: 'STRATEGY', title: 'Strategy' },
    { name: 'TVL', title: 'TVL/Capacity' },
    { name: 'APY_TITLE', title: 'Projected Annualized Returns' },
    { name: 'APY', title: 'Real-time', titleClass: APYClassName },
    { name: 'APY_W', title: '7-day', titleClass: APYClassName },
    { name: 'APY_S', title: '3-month', titleClass: APYClassName },
  ];

  const portfolioProperty: ICardInfo[] = [
    { name: 'STRATEGY', title: 'Strategy' },
    { name: 'TVL', title: 'TVL/Capacity' },
    { name: 'HOLDINGS', title: 'Current Value' },
    { name: 'PROFITS', title: 'Profits' },
  ];
  const investProperty: ICardInfo[] = [
    { name: 'HOLDINGS', title: 'Current Value' },
    { name: 'PROFITS', title: 'Profits' },
  ];
  const detailProperty: ICardInfo[] = [
    { name: 'TVL', title: 'TVL/Capacity' },
    { name: 'APY_TITLE', title: 'Projected Annualized Returns' },
    { name: 'APY', title: 'Real-time', titleClass: APYClassName },
    { name: 'APY_W', title: '7-day', titleClass: APYClassName },
    { name: 'APY_S', title: '3-month', titleClass: APYClassName },
    { name: 'LEVERAGE', title: 'Target Leverage' },
    { name: 'INCEPTION_DATE', title: 'Vault Inception Date' },
    { name: 'THRESHOLD', title: 'Price Movement Rebalance Threshold' },
    { name: 'FARMING_POOL', title: 'Underlying Farming Pool' },
  ];

  let displayData = (dataInfo: ICardInfo[]) =>
    dataInfo.map((item, i) => {
      return (
        <>
          <div key={i} className="info-row">
            <div className="fieldName">
              <p className={`subtitle-3 ${item.titleClass || ''}`}>
                {item.title}
              </p>
              {item.name === 'APY_TITLE' && (
                <QuestionMarkTooltip
                  title={`The 7-day and 3-month APYs are calculated based on real performance 
                    over the designated period of time, with all the fees included. 
                    The real-time APY is calculated assuming the same market condition 
                    where fees are not applicable.`}
                  placementPC={'bottom'}
                  placementMobile={'bottom'}
                  toolTipClassName={'apy-bottom-tooltip'}
                />
              )}
            </div>
            <div className={`fieldValue`}>
              <p className={`subtitle-1`}>
                {getColoredValue(item.name, props, infoProperty[item.name])}
              </p>
            </div>
          </div>
          {page === 'Details' && item.name === 'PROFITS' && <hr></hr>}
        </>
      );
    });

  return (
    <Fragment>
      {page === 'DeltaNeutral' && displayData(dnProperty)}
      {page === 'Portfolio' && displayData(portfolioProperty)}
      {page === 'Details' &&
        (connected && !Number.isNaN(holdings) && holdings > 0
          ? displayData(investProperty.concat(detailProperty))
          : displayData(detailProperty))}
    </Fragment>
  );
};
