const Flex = styled(Subtitle2)`
  display: flex;
  align-items: center;
  color: inherit;
  svg {
    margin-right: 6.75px;
  }
`;

export const StrategyDropdown: React.FC<StrategyDropdownProps> = ({
  tokens,
  charts,
  strategy,
  vaultItem,
  openStrategyDetails,
  MobileButtonGroup,
}) => {
  const { isSm } = useMatchBreakpoints();

  return (
    <div>
      <Tabs
        tabs={[
          {
            title: (
              <Flex>
                {!isSm ? (
                  <>
                    <Performance /> Performance Chart
                  </>
                ) : (
                  <>Overview</>
                )}
              </Flex>
            ),
            content: isSm ? (
              <>
                <Overview vaultItem={vaultItem} {...strategy} />
                <VaultInfo
                  strategy={strategy}
                  viewDetails={openStrategyDetails}
                />
                {MobileButtonGroup}
              </>
            ) : (
              <ChartsOverview charts={charts} tokens={tokens} openStrategyDetails={openStrategyDetails} />
            ),
          },
          {
            title: (
              <Flex>
                {!isSm ? (
                  <>
                    <Vault /> Vault Info
                  </>
                ) : (
                  <>Charts</>
                )}
              </Flex>
            ),
            content: !isSm ? (
              <VaultInfo
                strategy={strategy}
                viewDetails={openStrategyDetails}
              />
            ) : (
              <>
                <ChartsOverview charts={charts} tokens={tokens} />
                {MobileButtonGroup}
              </>
            ),
          },
          {
            title: (
              <Flex>
                {!isSm && <Money />}
                How It Works
              </Flex>
            ),
            content: (
              <>
                <HowItWorks
                  leverage={strategy.leverage}
                  tokenA={tokens[0]}
                  tokenB={tokens[1]}
                  vaultItem={vaultItem}
                />
                {isSm && MobileButtonGroup}
              </>
            ),
          },
        ]}
        openDetails={openStrategyDetails}
      />
    </div>
  );
};
