import { CrossIcon } from '@aperture/assetkit';
import {
  APY,
  StrategyDropdown,
  timestampToUTCString,
  TVLCapacity,
} from '@aperture/uikit';
import { Modal } from '@mui/material';
import { StrategyTypeTextMap } from 'constants/crosschain';
import {
  getMaximumDrawdown,
  getPerformanceData,
  getStrategyRebalance,
} from 'helpers/graph';
import { ReactNode, useMemo, useState } from 'react';
import styled from 'styled-components';
import { ProtocolInfo } from '../../../../../../constants/protocolConfig';
import { Strategy, StrategyInfo } from '../../../../../../constants/strategy';
import {
  useFetchBacktestAnalysis,
  useFetchRebalance,
  useFetchReturn,
  useFetchUserGraphInfo,
} from '../../../../../../features/graph/useFetchGraphData';
import {
  calculateThreshold,
  numberParseLarge,
  profitsDataParser,
  rebalanceDataParser,
} from '../../../../../../helpers/utilities';
import { PositionInfo } from '../../../../../../types';
import { PdnStrategyDetails } from '../PdnStrategyDetails';

interface PdnDropdownProps {
  protocols?: ProtocolInfo[];
  vaultTitle?: string;
  portfolio: boolean;
  strategyInfo: StrategyInfo;
  strategy: Strategy;
  positionInfo: PositionInfo | null;
  apy: APY;
  tvlCapacity: TVLCapacity;
  vaultItem: ReactNode;
}

const StyledCrossIcon = styled(CrossIcon)`
  path {
    fill: ${({ theme }) => theme.colors.text};
  }
`;

export const PdnDropdown = ({
  portfolio,
  vaultTitle = '',
  strategyInfo,
  strategy,
  positionInfo,
  apy,
  tvlCapacity,
  vaultItem,
}: PdnDropdownProps) => {
  const threshold = calculateThreshold(
    strategyInfo.priceMovementThreshold,
    strategyInfo.leverage
  );

  const Cross = () => (
    <div className="cross" onClick={() => setOpen(false)}>
      <StyledCrossIcon />
    </div>
  );
  const { data: backtestAnalysisData } = useFetchBacktestAnalysis();
  const { data: rebalance } = useFetchRebalance();
  const { data: returns } = useFetchReturn(strategy.strategyId);
  const { data: performance } = useFetchUserGraphInfo(
    positionInfo?.positionId ?? ''
  );
  const performanceList = getPerformanceData(
    performance,
    positionInfo?.equity,
    strategyInfo?.tokens[0].decimals
  );

  const [open, setOpen] = useState(false);

  const getFarmingPoolInfo = (tokens: any[], protocols: any[]) => {
    const tokenString = `${tokens[0].ticker} - ${tokens[1].ticker}`;
    if (protocols.length === 2) {
      const Icon1 = protocols[0].icon;
      const Icon2 = protocols[1].icon;
      return (
        <div className="farming-pool">
          <div>
            <Icon1 style={{ marginTop: '-7px', marginRight: '8px' }} />
            {`${protocols[0].name} ${tokenString}`}
          </div>
          <div>
            <Icon2 />
            {`${protocols[1].name} ${tokenString}`}
          </div>
        </div>
      );
    } else {
      const Icon = protocols[0].icon;
      return (
        <>
          <Icon
            style={{ position: 'relative', top: '5px', marginRight: '8px' }}
          />
          {`${protocols[0].name} ${tokenString}`}
        </>
      );
    }
  };
  const poolData = getFarmingPoolInfo(
    strategyInfo.tokens,
    strategyInfo.protocols
  );
  const inception_Date = useMemo(
    () =>
      returns && returns.inception.length > 0
        ? timestampToUTCString(
            returns.inception[0]!.timestamp_sec,
            'DayMonthYear'
          )
        : '--',
    [returns]
  );
  const return_Changes = useMemo(
    () =>
      returns && returns.inception.length > 0
        ? returns.inception[returns.inception.length - 1].percentage
        : Infinity,
    [returns]
  );
  const maximum_Drawdown = useMemo(
    () => getMaximumDrawdown(returns?.inception ?? []),
    [returns]
  );
  const number_Rebalance = useMemo(
    () =>
      rebalanceDataParser(
        getStrategyRebalance(rebalance ?? [], strategy.strategyId),
        returns && returns.inception.length > 0
          ? returns.inception[0]!.timestamp_sec
          : Infinity
      ),
    [returns, rebalance, strategy]
  );
  const profits_Text = useMemo(() => {
    const text_value =
      positionInfo && positionInfo.profits
        ? profitsDataParser(
            positionInfo.equity,
            positionInfo.profits,
            strategy.tokens[0].ticker
          )
        : '--';
    return positionInfo && positionInfo.profits
      ? [positionInfo.profits.profits, text_value]
      : ['', text_value];
  }, [positionInfo, strategy.tokens]);
  return (
    <>
      <StrategyDropdown
        charts={
          portfolio
            ? [
                {
                  chartsType: 'performance',
                  chartsData: performanceList,
                  chartsId: positionInfo?.positionId ?? '',
                },
                {
                  chartsType: 'return',
                  chartsData: returns,
                  chartsId: strategy.strategyId,
                },
              ]
            : [
                {
                  chartsType: 'return',
                  chartsData: returns,
                  chartsId: strategy.strategyId,
                },
                {
                  chartsType: 'backtest',
                  chartsData:
                    backtestAnalysisData &&
                    backtestAnalysisData[strategy.strategyId],
                  chartsId: strategy.strategyId,
                },
              ]
        }
        tokens={[strategy.tokens[0].ticker, strategy.tokens[1].ticker]}
        openStrategyDetails={() => setOpen(true)}
        strategy={{
          leverage: strategy.leverage,
          inceptionDate: inception_Date,
          returnChanges: return_Changes,
          threshold: threshold,
          sharpRatio: 5.28, //missing
          maxDrawdown: maximum_Drawdown,
          rebalances: number_Rebalance,
          underlyingPool: poolData,
          strategyType:
            StrategyTypeTextMap[strategy.strategyType] === 'bull'
              ? 'bull'
              : 'crab',
          tvlCapacity: tvlCapacity,
          apy: apy,
          position: {
            active: positionInfo ? true : false,
            currentValue: positionInfo
              ? `${numberParseLarge(
                  positionInfo?.userEquity ?? 0,
                  positionInfo?.userEquity ?? 0 > 0.1 ? 2 : 4
                )} ${strategy.tokens[0].ticker}`
              : '--',
            profits: profits_Text,
          },
        }}
        vaultItem={vaultItem}
      />
      <Modal
        open={open}
        onClose={() => setOpen(false)}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <div className={'modal-content modal-pdn-strategy-details'}>
          <Cross />
          <PdnStrategyDetails
            strategyInfo={strategyInfo}
            positionInfo={positionInfo ?? null}
            vaultTitle={vaultTitle}
          />
        </div>
      </Modal>
    </>
  );
};
