import { numberParse, Tag, useMatchBreakpoints } from '@aperture/uikit';
import Accordion from '@mui/material/Accordion';
import AccordionDetails from '@mui/material/AccordionDetails';
import AccordionSummary from '@mui/material/AccordionSummary';
import React, { Fragment, useEffect, useMemo, useState } from 'react';
import { PositionInfoMap } from '../../constants/position';

import { ProgressBar } from '@aperture/uikit';
import styled from 'styled-components';
import {
  DELISTING_POLICY,
  StrategyTitleTextMap,
  StrategyType,
  StrategyTypeTextMap,
} from '../../constants/crosschain';
import { Strategy, StrategyInfoMap } from '../../constants/strategy';
import { useFetchReturn } from '../../features/graph/useFetchGraphData';
import { useFetchEvmPosition } from '../../features/positions/hooks';
import { useFetchStrategy } from '../../features/strategies/hooks';
import { calculate_timeframe_apy, TimeframeType } from '../../helpers/graph';
import {
  BN,
  getProtocol,
  getVaultName,
  isTVLGreaterOrEqualToCapacity,
  numberParseLarge,
  profitsDataParser,
} from '../../helpers/utilities';
import { useStore } from '../../store';
import { useOpenModal } from '../../store/base/application/hooks';
import { ModalType } from '../../store/base/AppSlice';
import textContext from '../../textContext';
import { UnpopulatedPositionInfo } from '../../types';
import { Button, NumberCounter } from '../v1';
import { AccordionArrow } from '../v1/Components/AccordionArrow';
import { StrategyCard, VaultItem } from '../v1/Components/Strategy';
import { PdnDropdown } from '../v1/Components/Strategy/Dropdown/PdnDropdown';
import { ApyRow, AssetTableRow } from './Invest';
import TableLoader from './TableLoader';

interface ITableAssetsProps {
  info?: Object;
  isOpen: React.Dispatch<React.SetStateAction<boolean>>;
  // strategyInfo: StrategyInfo;
  strategy: Strategy;
  setStrategyInfoMap: React.Dispatch<React.SetStateAction<StrategyInfoMap>>;
  strategyOrder: number | null;
  positionInfo: UnpopulatedPositionInfo | null;
  key: number;
  title: string;
  setPositionMap: React.Dispatch<React.SetStateAction<PositionInfoMap>>;
  search: string;
  strategyType: string;
  assets: string[];
}

export const VaultContainer = styled.div`
  padding-left: 16px;
`;

const renderStrategy = (strategyType: StrategyType) => {
  switch (strategyType) {
    case StrategyType.PSEUDO_DELTA_NEUTRAL:
      return <Tag variant={'crab'} />;
    case StrategyType.PSEUDO_1X_LONG:
      return <Tag variant={'bull'} />;
  }
};
export default function TableAssets(props: ITableAssetsProps) {
  const { isSm } = useMatchBreakpoints();
  const language = useStore((state) => state.appSlice.language);
  const isWalletConnected = useStore(
    (state) => state.chainSlice.isWalletConnected
  );
  const investText = textContext[language]['investment']['invest'];
  const {
    strategy,
    positionInfo,
    title,
    setPositionMap,
    setStrategyInfoMap,
    strategyOrder,
    search,
    strategyType,
    assets,
  } = props;
  const [expanded, setExpanded] = useState(false);
  const [hover, setHover] = useState(false);
  const { data } = useFetchStrategy(strategy);
  const { isLoading: positionIsLoading, data: position } =
    useFetchEvmPosition(positionInfo);
  const { data: returns } = useFetchReturn(strategy.strategyId);

  const vaultItem = (
    <VaultItem
      {...{
        title,
        leverage: strategy.leverage,
        tokens: strategy.tokens,
        protocols: strategy.protocols,
        strategyType: strategy.strategyType,
      }}
    />
  );

  const openInvestModal = useOpenModal(ModalType.INVEST, {
    invest: true,
    strategyInfo: data,
    positionInfo: position,
    vaultItem,
    title: StrategyTitleTextMap[strategy.strategyType],
  });

  const handleInvest = (e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
    e.stopPropagation();
    openInvestModal();
    props.isOpen(true);
  };

  useEffect(() => {
    if (positionIsLoading || !position?.equity || !data?.strategyId) {
      return;
    }
    setPositionMap((prev: PositionInfoMap) => ({
      ...prev,
      [data.strategyId]: position,
    }));
  }, [data, position, positionIsLoading, setPositionMap]);

  const tvlGteCapacity = useMemo(
    () =>
      data
        ? isTVLGreaterOrEqualToCapacity(data.TVL, data.capacity, data.leverage)
        : false,
    [data]
  );

  const apy_7days = useMemo(
    () =>
      returns?.week
        ? calculate_timeframe_apy(returns?.week, TimeframeType.WEEK)
        : undefined,
    [returns?.week]
  );

  const apy_season = useMemo(
    () =>
      returns?.season
        ? calculate_timeframe_apy(returns?.season, TimeframeType.SEASON)
        : undefined,
    [returns?.season]
  );

  const apy_data = useMemo(
    () => [data?.apy, apy_7days, apy_season],
    [data?.apy, apy_7days, apy_season]
  );

  const profits_Text = useMemo(() => {
    const text_value =
      positionInfo && position && position.profits
        ? profitsDataParser(
            position.equity,
            position.profits,
            strategy.tokens[0].ticker
          )
        : '--';
    return positionInfo && position && position.profits
      ? [position.profits.profits, text_value]
      : ['', text_value];
  }, [positionInfo, position, strategy.tokens]);

  useEffect(() => {
    if (data?.strategyId) {
      setStrategyInfoMap((prev: StrategyInfoMap) => {
        return {
          ...prev,
          [data.strategyId]: {
            ...data,
            apy_7days: apy_7days ?? undefined,
            apy_season: apy_season ?? undefined,
          },
        };
      });
    }
  }, [apy_7days, apy_season, data, setStrategyInfoMap]);

  const shouldShow = useMemo(
    () =>
      (!(DELISTING_POLICY && strategy.hiddenFromNewUsers) ||
        (DELISTING_POLICY &&
          position !== null &&
          position !== undefined &&
          BN(position?.equity).gt(0))) &&
      (getVaultName(strategy.tokens)
        .toLowerCase()
        .includes(search.toLowerCase()) ||
        getProtocol(strategy.protocols)
          .toLowerCase()
          .includes(search.toLowerCase()) ||
        StrategyTypeTextMap[strategy.strategyType]
          .toLowerCase()
          .includes(search.toLowerCase())) &&
      ((strategyType !== '' && strategy.strategyType === strategyType) ||
        strategyType === '') &&
      ((assets.length === 1 && assets[0] === '') ||
        assets.filter(
          (item) =>
            item === strategy.tokens[0].ticker.toLowerCase() ||
            item === strategy.tokens[1].ticker.toLowerCase()
        ).length > 0),
    [search, strategy, strategyType, assets, position]
  );

  return shouldShow ? (
    <Fragment>
      {!isSm && (
        <Accordion
          onMouseEnter={() => setHover(true)}
          onMouseLeave={() => setHover(false)}
          expanded={expanded}
          onChange={(e, expanded) => setExpanded(expanded)}
          sx={{ order: strategyOrder }}
        >
          <AccordionSummary>
            <AssetTableRow>
              <VaultContainer>{vaultItem} </VaultContainer>
              <div>
                <div className="subtitle-2">
                  {strategy.strategyType
                    ? renderStrategy(strategy.strategyType)
                    : '-'}
                </div>
              </div>
              <div className="capacity-group">
                {data?.TVL !== undefined &&
                data?.capacity !== undefined &&
                data?.leverage !== undefined ? (
                  <div>
                    <span className="subtitle-2">
                      {data?.TVL === null ? (
                        '-'
                      ) : (
                        <NumberCounter
                          value={data!.TVL}
                          formatNumberFunction={(value: string | number) =>
                            '$' + numberParseLarge(value, 2) + ' '
                          }
                        />
                      )}
                    </span>
                    {` / `}
                    <span className="subtitle-2" style={{ marginRight: '5px' }}>
                      {data?.capacity === null ? (
                        '-'
                      ) : (
                        <NumberCounter
                          value={data!.capacity}
                          formatNumberFunction={(value: string | number) =>
                            numberParseLarge(value, 2)
                          }
                        />
                      )}
                    </span>
                    <br />
                    <ProgressBar
                      percentage={Number(data!.TVL) / Number(data!.capacity)}
                    />
                  </div>
                ) : (
                  <TableLoader size={3} />
                )}
              </div>
              <ApyRow>
                {apy_data.map((item, index) => (
                  <div className={`subtitle-2`} key={index}>
                    {item !== undefined && item !== null ? (
                      <NumberCounter
                        value={item}
                        colored={true}
                        formatNumberFunction={(value: string | number) =>
                          numberParse(value, 'percentage')
                        }
                      />
                    ) : item === null ? (
                      '--%'
                    ) : (
                      <TableLoader size={3} />
                    )}
                  </div>
                ))}
              </ApyRow>
              <div className="investButton">
                <Button
                  className="action-deposit"
                  variant="outline"
                  size="small"
                  text={investText['action' as keyof object]}
                  onClick={(event) => handleInvest(event)}
                  disabled={tvlGteCapacity || !isWalletConnected}
                  style={{ padding: '0px 16px' }}
                />
              </div>
              <AccordionArrow {...{ expanded, hover }} />
            </AssetTableRow>
          </AccordionSummary>
          <AccordionDetails>
            {data ? (
              <PdnDropdown
                positionInfo={position ?? null}
                strategyInfo={data ?? null}
                strategy={strategy}
                protocols={strategy.protocols}
                vaultTitle={props.title}
                portfolio={false}
                apy={{
                  apy: Number(data?.apy ?? Infinity),
                  apy_7days: apy_7days ?? Infinity,
                  apy_season: apy_season ?? Infinity,
                }}
                tvlCapacity={{
                  content: `$${numberParseLarge(
                    data!.TVL ?? 0,
                    2
                  )} / $${numberParseLarge(data!.capacity ?? 0, 2)}`,
                  percentage: Number(data!.TVL) / Number(data!.capacity),
                }}
                vaultItem={vaultItem}
              />
            ) : (
              <TableLoader />
            )}
          </AccordionDetails>
        </Accordion>
      )}

      {isSm && (
        <StrategyCard
          strategyInfo={data ?? null}
          positionInfo={position ?? null}
          profits={profits_Text}
          strategy={strategy}
          pageType="DeltaNeutral"
          walletConnected={isWalletConnected}
          tvlGteCapacity={tvlGteCapacity}
        />
      )}
    </Fragment>
  ) : null;
}
