import Grid from '@mui/material/Grid';
import React, { Fragment, useEffect, useMemo, useState } from 'react';
import Accordion from '@mui/material/Accordion';
import AccordionDetails from '@mui/material/AccordionDetails';
import AccordionSummary from '@mui/material/AccordionSummary';
import { ColoredValue, Tag, useMatchBreakpoints } from '@aperture/uikit';

import { StrategyTitleTextMap, StrategyType } from '../../constants/crosschain';
import { Strategy, StrategyInfoMap } from '../../constants/strategy';
import { useFetchStrategy } from '../../features/strategies/hooks';
import {
  isTVLGreaterOrEqualToCapacity,
  numberParseLarge,
  profitsDataParser,
} from '../../helpers/utilities';
import { useStore } from '../../store';
import { useOpenModal } from '../../store/base/application/hooks';
import { ModalType } from '../../store/base/AppSlice';
import textContext from '../../textContext';
import { PositionInfo } from '../../types';
import { VaultContainer } from '../Investment/TableAssets';
import TableLoader from '../Investment/TableLoader';
import { Button, NumberCounter } from '../v1';
import { AccordionArrow } from '../v1/Components/AccordionArrow';
import { ProgressBar } from '@aperture/uikit';
import { StrategyCard, VaultItem } from '../v1/Components/Strategy';
import { PdnDropdown } from '../v1/Components/Strategy/Dropdown/PdnDropdown';

interface ITableAssetsProps {
  isExtra?: boolean;
  info?: Object;
  isOpen: React.Dispatch<React.SetStateAction<boolean>>;
  strategy: Strategy;
  setStrategyInfoMap: React.Dispatch<React.SetStateAction<StrategyInfoMap>>;
  positionInfo: PositionInfo;
  title: string;
  strategyOrder: number;
}

export default function TablePositions(props: ITableAssetsProps) {
  const language = useStore((state) => state.appSlice.language);

  const isWalletConnected = useStore(
    (state) => state.chainSlice.isWalletConnected
  );

  const text = textContext[language as keyof object]['portfolio']['asset'];
  const { strategy, positionInfo, title, strategyOrder, setStrategyInfoMap } =
    props;
  const { isLoading: strategyIsLoading, data } = useFetchStrategy(strategy);
  const [expanded, setExpanded] = useState(false);
  const [hover, setHover] = useState(false);

  const tvlGteCapacity = useMemo(
    () =>
      data
        ? isTVLGreaterOrEqualToCapacity(data.TVL, data.capacity, data.leverage)
        : false,
    [data]
  );

  const profits_Text = useMemo(() => {
    const text_value =
      positionInfo && positionInfo.profits
        ? profitsDataParser(
          positionInfo.equity,
          positionInfo.profits,
          strategy.tokens[0].ticker
        )
        : '--';
    return positionInfo && positionInfo.profits
      ? [positionInfo.profits.profits, text_value]
      : ['', text_value];
  }, [positionInfo, strategy.tokens]);

  useEffect(() => {
    if (data?.strategyId) {
      setStrategyInfoMap((prev: StrategyInfoMap) => {
        return {
          ...prev,
          [data.strategyId]: {
            ...data,
          },
        };
      });
    }
  }, [data, setStrategyInfoMap]);

  const vaultItem = () => {
    return (
      <VaultItem
        {...{
          leverage: strategy.leverage,
          title: title,
          tokens: strategy.tokens,
          protocols: strategy.protocols,
          strategyType: strategy.strategyType,
        }}
      />
    );
  };

  const handleInvest = (e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => {
    e.stopPropagation();
    openInvestModal();
  };

  const handleWithdraw = (
    e: React.MouseEvent<HTMLButtonElement, MouseEvent>
  ) => {
    e.stopPropagation();
    openWithdrawModal();
  };

  const modalProps = {
    strategyInfo: data,
    positionInfo,
    vaultItem: vaultItem(),
    title: StrategyTitleTextMap[strategy.strategyType],
  };
  const openInvestModal = useOpenModal(ModalType.INVEST, {
    invest: true,
    ...modalProps,
  });

  const openWithdrawModal = useOpenModal(ModalType.INVEST, {
    invest: false,
    ...modalProps,
  });
  const { isSm } = useMatchBreakpoints();
  const renderStrategy = (strategyType: StrategyType) => {
    switch (strategyType) {
      case StrategyType.PSEUDO_DELTA_NEUTRAL:
        return <Tag variant={'crab'} />;
      case StrategyType.PSEUDO_1X_LONG:
        return <Tag variant={'bull'} />;
    }
  };

  return (
    <Fragment>
      {!isSm && (
        <Accordion
          onMouseEnter={() => setHover(true)}
          onMouseLeave={() => setHover(false)}
          onChange={(e, expanded) => setExpanded(expanded)}
          sx={{ order: strategyOrder }}
        >
          <AccordionSummary>
            <Grid container columns={10}>
              <Grid item md={2.1}>
                {<VaultContainer>{vaultItem()}</VaultContainer>}
              </Grid>
              <Grid item md={0.92}>
                <div className="subtitle-2">
                  {strategy.strategyType
                    ? renderStrategy(strategy.strategyType)
                    : '-'}
                </div>
              </Grid>
              <Grid item md={1.53} className="capacity-group">
                {strategyIsLoading || !data || !data.TVL || !data.capacity ? (
                  <TableLoader size={8} />
                ) : (
                  <>
                    <span className="subtitle-2">
                      <NumberCounter
                        value={data.TVL}
                        formatNumberFunction={(value) =>
                          `$${numberParseLarge(value, 2)}`
                        }
                      />
                    </span>
                    {` / `}
                    <span className="subtitle-2" style={{ marginRight: '5px' }}>
                      <NumberCounter
                        value={Number(data.capacity)}
                        formatNumberFunction={(value) =>
                          numberParseLarge(value, 2)
                        }
                      />
                    </span>
                    <ProgressBar percentage={Number(data?.TVL) / Number(data?.capacity)} />
                  </>
                )}
              </Grid>
              <Grid item md={1.48}>
                <div className="subtitle-2">
                  {
                    <NumberCounter
                      value={Number(positionInfo.userEquity)}
                      formatNumberFunction={(value) =>
                        `${numberParseLarge(
                          value,
                          Number(positionInfo.userEquity) > 0.1 ? 2 : 4
                        )} ${strategy.tokens[0].ticker}`
                      }
                    />
                  }
                </div>
              </Grid>
              <Grid item md={1.64}>
                <div className={`subtitle-2`}>
                  {positionInfo && positionInfo.profits && profits_Text ? (
                    <ColoredValue value={Number(profits_Text[0])}>
                      {profits_Text[1]}
                    </ColoredValue>
                  ) : (
                    '-'
                  )}
                </div>
              </Grid>
              <Grid item md={1.91} className="action-group">
                <div>
                  {!props.isExtra && (
                    <Button
                      variant="outline"
                      size="small"
                      text={text.invest}
                      disabled={tvlGteCapacity}
                      onClick={(event) => handleInvest(event)}
                      style={{
                        padding: '1px 9px',
                        width: '96px',
                        marginRight: '16px',
                      }}
                    />
                  )}

                  <Button
                    variant="info"
                    size="small"
                    text={text.withdraw}
                    disabled={Number(positionInfo.equity) > 0 ? false : true}
                    onClick={(event) => handleWithdraw(event)}
                    style={{ padding: `1px 9px`, width: '96px' }}
                  />
                </div>
              </Grid>
              <Grid item md={0.42}>
                <AccordionArrow {...{ expanded, hover }} />
              </Grid>
            </Grid>
          </AccordionSummary>
          <AccordionDetails>
            {data && (
              <PdnDropdown
                strategyInfo={data ?? {}}
                positionInfo={positionInfo}
                protocols={data.protocols}
                vaultTitle={props.title}
                portfolio={true}
                strategy={strategy}
              />
            )}
          </AccordionDetails>
        </Accordion>
      )}
      {isSm && (
        <StrategyCard
          strategyInfo={data ?? null}
          strategy={strategy}
          pageType="Portfolio"
          positionInfo={positionInfo}
          walletConnected={isWalletConnected}
          profits={profits_Text}
          tvlGteCapacity={tvlGteCapacity}
          portfolio={true}
        />
      )}
    </Fragment>
  );
}
