import React, { useMemo } from 'react';
import { Strategy, StrategyInfo } from '../../../../../../constants/strategy';
import {
  useFetchBacktestAnalysis,
  useFetchRebalance,
  useFetchReturn,
  useFetchUserGraphInfo,
} from '../../../../../../features/graph/useFetchGraphData';
import {
  calculate_timeframe_apy,
  getMaximumDrawdown,
  getPerformanceData,
  getStrategyRebalance,
  TimeframeType,
} from '../../../../../../helpers/graph';
import {
  calculateThreshold,
  getVaultName,
  numberParseLarge,
  rebalanceDataParser,
  timestampToUTCString,
} from '../../../../../../helpers/utilities';
import { PositionInfo } from '../../../../../../types';
import { Button } from '../../../Button';
import { VaultItem } from '../../VaultItem';
import './tab.scss';
// Zustand Store
import { StrategyDropdown } from '@aperture/uikit';
import { StrategyTypeTextMap } from 'constants/crosschain';
import { ProtocolInfo } from 'constants/protocolConfig';
import { TokenInfo } from 'constants/tokenConfig';
import styled from 'styled-components';

interface DetailsProps {
  clickDeposit: (e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => void;
  clickWithdraw: (e: React.MouseEvent<HTMLButtonElement, MouseEvent>) => void;
  strategyInfo: StrategyInfo | null;
  strategy: Strategy;
  walletConnected: boolean;
  pageType: 'Portfolio' | 'Details' | 'DeltaNeutral';
  positionInfo: PositionInfo | null;
  holdings: number;
  profits: string[];
  tvlGteCapacity: boolean;
  portfolio?: boolean;
}

const ButtonGroupStyled = styled.div`
  padding: 0 24px;
  display: flex;
  justify-content: space-between;
`;

export const StrategyTabs = ({
  portfolio = false,
  walletConnected = false,
  pageType = 'DeltaNeutral',
  positionInfo,
  strategyInfo,
  holdings,
  profits,
  clickDeposit,
  clickWithdraw,
  tvlGteCapacity,
  strategy,
  ...props
}: DetailsProps) => {
  const { data: backtestAnalysisData } = useFetchBacktestAnalysis();
  const { data: rebalance } = useFetchRebalance();
  const { data: returns } = useFetchReturn(strategy.strategyId);
  const { data: performance } = useFetchUserGraphInfo(
    positionInfo?.positionId ?? ''
  );
  const performanceList = getPerformanceData(
    performance,
    positionInfo?.equity,
    strategy.tokens[0].decimals
  );

  const title = useMemo(
    () =>
      strategyInfo?.tokens && strategyInfo.leverage
        ? `${getVaultName(strategyInfo.tokens)}`
        : ``,
    [strategyInfo]
  );

  const apy_7days = useMemo(
    () =>
      returns?.week
        ? calculate_timeframe_apy(returns?.week, TimeframeType.WEEK)
        : undefined,
    [returns?.week]
  );

  const apy_season = useMemo(
    () =>
      returns?.season
        ? calculate_timeframe_apy(returns?.season, TimeframeType.SEASON)
        : undefined,
    [returns?.season]
  );

  const vaultItem = (
    <VaultItem
      {...{
        leverage: strategy.leverage,
        title,
        tokens: strategy.tokens,
        protocols: strategy.protocols,
        strategyType: strategy.strategyType,
      }}
    />
  );

  const getFarmingPoolInfo = (
    tokens: TokenInfo[],
    protocols: ProtocolInfo[]
  ) => {
    const tokenString = getVaultName(tokens);
    if (protocols.length === 2)
      return (
        <>
          {`${protocols[0].name} ${tokenString}`}
          <br />
          {`${protocols[1].name} ${tokenString}`}
        </>
      );
    else return <>{`${protocols[0].name} ${tokenString}`}</>;
  };

  const inception_Date = useMemo(
    () =>
      returns && returns.inception.length > 0
        ? timestampToUTCString(
            returns.inception[0]!.timestamp_sec,
            'DayMonthYear'
          )
        : '--',
    [returns]
  );

  const return_Changes = useMemo(
    () =>
      returns && returns.inception.length > 0
        ? returns.inception[returns.inception.length - 1].percentage
        : Infinity,
    [returns]
  );

  const maximum_Drawdown = useMemo(
    () => getMaximumDrawdown(returns?.inception ?? []),
    [returns]
  );

  const number_Rebalance = useMemo(
    () =>
      rebalanceDataParser(
        getStrategyRebalance(rebalance ?? [], strategy.strategyId),
        returns && returns.inception.length > 0
          ? returns.inception[0]!.timestamp_sec
          : Infinity
      ),
    [returns, rebalance, strategy]
  );

  const threshold =
    strategyInfo === null
      ? Infinity
      : calculateThreshold(
          strategyInfo.priceMovementThreshold,
          strategyInfo.leverage
        );

  return (
    <StrategyDropdown
      {...props}
      charts={
        portfolio
          ? [
              {
                chartsType: 'performance',
                chartsData: performanceList,
                chartsId: positionInfo?.positionId ?? '',
              },
              {
                chartsType: 'return',
                chartsData: returns,
                chartsId: strategy.strategyId,
              },
            ]
          : [
              {
                chartsType: 'return',
                chartsData: returns,
                chartsId: strategy.strategyId,
              },
              {
                chartsType: 'backtest',
                chartsData:
                  backtestAnalysisData &&
                  backtestAnalysisData[strategy.strategyId],
                chartsId: strategy.strategyId,
              },
            ]
      }
      tokens={[strategy.tokens[0].ticker, strategy.tokens[1].ticker]}
      openStrategyDetails={() => {}}
      strategy={{
        leverage: strategy.leverage,
        inceptionDate: inception_Date,
        returnChanges: return_Changes,
        threshold: threshold,
        sharpRatio: 5.28, //missing
        maxDrawdown: maximum_Drawdown,
        rebalances: number_Rebalance,
        underlyingPool: getFarmingPoolInfo(strategy.tokens, strategy.protocols),
        strategyType:
          StrategyTypeTextMap[strategy.strategyType] === 'bull'
            ? 'bull'
            : 'crab',
        tvlCapacity: {
          content:
            strategyInfo?.TVL && strategyInfo?.capacity
              ? `$${numberParseLarge(strategyInfo.TVL, 2)} / ${numberParseLarge(
                  strategyInfo.capacity,
                  2
                )}`
              : '--',
          percentage:
            Number(strategyInfo?.TVL ?? 0) /
            Number(strategyInfo?.capacity ?? 1),
        },
        apy: {
          apy: Number(strategyInfo?.apy ?? Infinity),
          apy_7days: apy_7days ?? Infinity,
          apy_season: apy_season ?? Infinity,
        },
        position: {
          active: portfolio && positionInfo ? true : false,
          currentValue: `${numberParseLarge(
            positionInfo?.userEquity ?? 0,
            positionInfo?.userEquity ?? 0 > 0.1 ? 2 : 4
          )} ${strategy.tokens[0].ticker}`,
          profits: profits,
        },
      }}
      vaultItem={vaultItem}
      MobileButtonGroup={
        <ButtonGroupStyled>
          <Button
            variant="contained"
            size="small"
            text={'Deposit'}
            disabled={tvlGteCapacity || !walletConnected}
            onClick={(event) => clickDeposit(event)}
            style={{ width: '40%', padding: '3px' }}
          />
          <Button
            variant="info"
            size="small"
            text={'Withdraw'}
            disabled={!(walletConnected && holdings > 0)}
            onClick={(event) => clickWithdraw(event)}
            style={{ width: '40%', padding: '3px' }}
          />
        </ButtonGroupStyled>
      }
    />
  );
};
