import { createContext } from 'react';
import * as AWS from 'aws-sdk';

export const AWSDocumentClientContext = createContext({});
const keyConfiguration = {
  accessKeyId: process.env.REACT_APP_AWS_ACCESS_KEY,
  secretAccessKey: process.env.REACT_APP_AWS_SECRET_KEY,
};

const testConnection = async (configuration: any) => {
  AWS.config.update(configuration);
  const ddb = new AWS.DynamoDB({ apiVersion: '2012-08-10' });
  const connectionTestParams = {
    ExpressionAttributeValues: {
      ':id': { N: '0' },
    },
    KeyConditionExpression: 'id = :id',
    TableName: 'aperture_whitelist_address'
  };
  const start = new Date().getTime();
  await ddb.query(connectionTestParams).promise();
  const end = new Date().getTime();
  return end - start;
}
const testConnections = async () => {
  let connectionTimes = [];
  connectionTimes.push({ region: 'eu-west-2', time: await testConnection({ ...keyConfiguration, region: 'eu-west-2' }) });
  connectionTimes.push({ region: 'ap-southeast-1', time: await testConnection({ ...keyConfiguration, region: 'ap-southeast-1' }) });
  connectionTimes.push({ region: 'us-west-2', time: await testConnection({ ...keyConfiguration, region: 'us-west-2' }) });
  return connectionTimes.reduce((prev, curr) => prev.time > curr.time ? curr : prev).region;
};

export const documentClient = testConnections().then(region => {
  AWS.config.update({ ...keyConfiguration, region });
  return new AWS.DynamoDB.DocumentClient();
});
