import {
  MetamaskIcon,
  OkxDarkIcon,
  OkxIcon,
  WalletConnectIcon,
  WalletIcon,
} from '@aperture/assetkit';
import { IStore, useStore } from 'store';
import styled from 'styled-components';
import { Blockchain, chainList } from '../../../../constants/crosschain';
import { Button } from '../../Components/Button';
import { Modal } from '../../Modal/Modal';
import { WalletConnect } from '../../Wallet';
import { SelectNetwork } from '../../Wallet/SelectNetwork';
import './connect.scss';

interface ConnectProps {
  mobile: boolean;
  walletConnected: boolean;
  onChainSelect: (chainId: any) => void;
  onWalletSelect: (walletType: any) => void;
  chain: Blockchain | null;
}

const StyledWalletIcon = styled(WalletIcon)`
  path {
    fill: ${({ theme }) => theme.colors.textAlt};
  }
  rect {
    fill: ${({ theme }) => theme.colors.textAlt};
  }
`;

export const Connect = ({
  mobile,
  walletConnected,
  onChainSelect,
  onWalletSelect,
  chain,
  ...props
}: ConnectProps) => {
  const isDarkMode = useStore((state: IStore) => state.themeSlice.darkMode);
  return (
    <>
      {!walletConnected && (
        <Modal
          trigger={
            <Button
              {...props}
              className="connect-wallet"
              variant="contained"
              size="small"
              disabled={walletConnected}
            >
              {' '}
              <>
                <StyledWalletIcon width="17.5" />
                <span
                  className="button-3"
                  style={{ marginLeft: mobile ? 4 : 10 }}
                >
                  {' '}
                  {mobile ? 'connect' : 'connect wallet'}
                </span>
              </>
            </Button>
          }
        >
          <SelectNetwork
            chainList={chainList}
            selectedChain={chain}
            onSelect={(chain) => {
              onChainSelect(chain);
            }}
          />

          <WalletConnect
            walletList={[
              {
                walletType: 'injected',
                name: 'MetaMask',
                icon: MetamaskIcon,
              },
              {
                walletType: 'custom-okxwallet',
                name: 'OKX Wallet',
                icon: isDarkMode ? OkxDarkIcon : OkxIcon,
              },

              {
                walletType: 'walletconnect',
                name: 'Wallet Connect',
                icon: WalletConnectIcon,
              },
            ]}
            onChainSelect={onChainSelect}
            onWalletSelect={onWalletSelect}
          />
        </Modal>
      )}
    </>
  );
};
