import React, { useRef, useState, useLayoutEffect } from 'react';
import { ResponsiveContainer as RC } from 'recharts';
import { RCProps } from '.'

export const ResponsiveContainer: React.FC<React.PropsWithChildren<RCProps>> = ({ id, children, ...props }) => {

  const [height, setHeight] = useState(0);
  const parentRef = useRef(null);
  useLayoutEffect(() => {
    function updateSize() {
      const element = document.getElementById(id);
      if (element) setHeight(element.offsetWidth * 0.45);
    }
    window.addEventListener('resize', updateSize);
    updateSize();
    return () => window.removeEventListener('resize', updateSize);
  }, []);

  return (
    <div id={id} ref={parentRef} {...props}>
      <RC width="100%" height={height}>
        <>{children}</>
      </RC>
    </div>)
}
