import {
  forwardRef,
  PropsWithChildren,
  useRef,
  useImperativeHandle,
} from 'react';
import './modal.scss';
import * as React from 'react';
import { Modal as MuiModal } from '@mui/material';
import styled from 'styled-components';
import { CrossIcon } from '@aperture/assetkit';

interface ModalProps {
  trigger: any;
  modalClassName?: string;
}

const StyledCrossIcon = styled(CrossIcon)`
  path {
    fill: ${({ theme }) => theme.colors.text};
  }
`;

export const Modal = forwardRef<ModalProps, PropsWithChildren<any>>(
  ({ trigger, modalClassName, children, ...props }, ref) => {
    const domRef = useRef<HTMLDivElement>(null);
    useImperativeHandle(ref, () => domRef.current!);

    const [open, setOpen] = React.useState(false);

    const Cross = () => (
      <div className="cross" onClick={() => setOpen(false)}>
        <StyledCrossIcon />
      </div>
    );
    return (
      <>
        <div
          onClick={() => setOpen(true)}
          ref={domRef}
          {...props}
          className="hover"
        >
          {trigger}
        </div>
        <MuiModal
          open={open}
          onClose={() => setOpen(false)}
          aria-labelledby="modal-modal-title"
          aria-describedby="modal-modal-description"
        >
          <div
            className={'modal-content'.concat(` ${modalClassName as string}`)}
          >
            <Cross />
            {children}
          </div>
        </MuiModal>
      </>
    );
  }
);
